<template>
    <section>
        <div class="my-popups" >
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('GeneralTimesheet') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopup"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>            
                    <div class="modal-body">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="row mt-0">
                                    <div class="col-sm-12 col-md-6 col-lg-5 col-xl-5 float-left pr-1 pl-0 mb-1">
                                        <div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                                            <div class="form-control">
                                                <label>Date Range: {{ this.Timesheetdata.TIMESHEET_NAME }}</label>&nbsp;<a id="aDateLable" class="pr-2 text-dark" style="cursor:default"></a>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left pr-1 pl-0 mb-1">
                                        <div class="form-group dpddown">
                                            <select id="ddlUser" v-model="modeluser" class="form-control" name="ddlUser"> 
                                                <option>Select User</option>
                                                <option v-for="option in DataRec" :value="option.value" selected>{{ option.name }}</option>                                                    
                                        </select> 
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-4 col-lg-3 col-xl-3 float-left pr-1 pl-0 mb-1">
                                        <div class="search-btm-btn background-0 text-left form-check-inline">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                            <div class="listing">
                <div class="col-10 mb-2 float-left p-0">
               
                    <div class="mr-2 d-inline-block">
                        <span class="lg-orange colorboxsquare"></span>{{ $t('PausedResumedTrackEntries') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends-purple colorboxsquare"></span> {{ $t('LeaveHoliday') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="lg-flaggedentry colorboxsquare"></span>{{ $t('UnPaidLeaveRecords') }}
                    </div>
                </div>
                        </div>
                  
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '@/services/DataService';

export default{
    props: {
        Timesheetdata: {
            type: Array,
            required: true
        },
        // UserData: {
        //     type: Array,
        //     required: true
        // },
        SingleUserData:{
            type: Array,
            required: true
        }
    },
    data(){
        return{
            UserData:[],
            modeluser:null,
            objData:{},
            DataRec:[],
            UserIdd:null
        }
    },
    created(){
        debugger;
        this.DataRec=[{
        value :this.GetUserInfo.ID,
         name :this.GetUserInfo.Name}
        ],
        this.modeluser = this.DataRec[0].value
      
        this.UserData;
        //  console.log('Timesheetdata',this.Timesheetdata);
        //this.GetUsersForTimesheet();
        
    },
    methods:{
      
        GetListData(){
             var VueObj = this;
            var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.modeluser}&dateFrom=${fDate}&dateTo=${tDate}`;
            VueObj.buttonShowHide()
            DataService.ListTimesheetForWeekNew(url).then(response => {
                if (response.data != null && response.data != "" && response.data.length > 0) {
                    let headerList1 = Object.keys(response.data[0]).filter(key => key !== 'Type' && key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail' && key != 'TaskDescription');
                    VueObj.isLoading = false;
                    var count;
                    this.HeaderList = headerList1;

                    VueObj.TotalRecords = response.data.length;
                    VueObj.TimesheetList = response.data.filter((item) => item.Type == 'TimeTrack');
                    VueObj.BreakList = response.data.filter((item) => item.Type == 'Break');
                    VueObj.LeaveList = response.data.filter(item => item.Type == 'Leave');

                    VueObj.TimesheetList.map((item, index) => {
                        //this.isOldTaskDesc[index] = true;
                        this.$set(item, 'RowNumber', index + 1);
                        item.OldTaskDescription = item.TaskDescription
                        return item
                    });

                    console.log('With Row Number', VueObj.TimesheetList);

                    var scol = 0;
                    var totalSeconds = 0;
                    VueObj.SumRowColTotalList()
                    VueObj.checkHolidayDate()
                } else {
                    VueObj.noRecord = true;
                    VueObj.isLoading = false;
                    VueObj.TimesheetList = [];
                }

            });
        },
        ClosePopup(){
            var vm = this;
            vm.$parent.GeneralTimesheetClosePopup();
            },
    }
}
</script>
